import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`
  /* Meyer's CSS RESET */
  
  /* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
	*/

	html, body, div, span, applet, object, iframe,
	h1, h2, h3, h4, h5, h6, p, blockquote, pre,
	a, abbr, acronym, address, big, cite, code,
	del, dfn, em, img, ins, kbd, q, s, samp,
	small, strike, strong, sub, sup, tt, var,
	b, u, i, center,
	dl, dt, dd, ol, ul, li,
	fieldset, form, label, legend,
	table, caption, tbody, tfoot, thead, tr, th, td,
	article, aside, canvas, details, embed, 
	figure, figcaption, footer, header, hgroup, 
	menu, nav, output, ruby, section, summary,
	time, mark, audio, video {
		margin: 0;
		padding: 0;
		border: 0;
		font-size: 100%;
		font: inherit;
		vertical-align: baseline;
	}
	/* HTML5 display-role reset for older browsers */
	article, aside, details, figcaption, figure, 
	footer, header, hgroup, menu, nav, section {
		display: block;
	}
	body {
		line-height: 1;
	}
	ol, ul {
		list-style: none;
	}
	blockquote, q {
		quotes: none;
	}
	blockquote:before, blockquote:after,
	q:before, q:after {
		content: '';
		content: none;
	}
	table {
		border-collapse: collapse;
		border-spacing: 0;
	}

	/* Global styles */
	*,
	*::before,
	*::after {
		box-sizing: border-box;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: auto;
	}

	#___gatsby {
		/* Ez kreál egy stacking contex-et z-index nélkül.
		Ez segít, hogy minden 'portal' kontent (modal, tooltip)
		mindenképpen a Gatsby app felett legyen majd. */
	
		isolation: isolate;
	}

	/* General styles */
	html, body, #___gatsby, #gatsby-focus-wrapper {
		height: 100%;
	}

	/* html {
		scroll-behavior: smooth;
	} */

	/* #gatsby-focus-wrapper {
		display: flex;
  	flex-direction: column;
		min-height: 100%;
	} */

	body {
		background-color: var(--color-background);
		overflow-y: scroll;
		line-height: 1.45;
	}

	a:focus {
		outline: 5px auto var(--color-base-dark)
	}

	h2 {
		margin-bottom: ${48 / 16}rem;
	}

	section :is(h1, h2, h3, h4, h5, h6, p, a)::selection {
		background-color: var(--color-base-dark);
		color: var(--color-primary-variant);
	}

	// TODO: delete this
	.page-section {
		padding-top: ${80 / 16}rem; // 80px
		min-height: 60vh;
	}

	/* Media Queries */
	@media (max-width: 768px) {
		html {
			font-size: 12px;
		}
		.page-section {
			padding-top: ${40 / 16}rem; // 80px
		}
	}

	/* CSS Variables */
	:root {
		--color-primary: #FAFAE9;
		--color-primary-variant: #f4eed7;
  	--color-secondary: #ecece6;
  	--color-secondary-variant: #c7c7b7;
  	--color-base-dark: #37372d;
  	--color-accent-01: #d7def4;
  	--color-accent-02: #e2f1d1;
  	--color-background: #f6f6f4;
		--color-white: #ffffff;

		--font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
		--font-family-headings: 'OldStyle', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

		--font-weight-light: 300;
		--font-weight-regular: 400;
		--font-weight-medium: 500;
		--font-weight-bold: 700;

		--font-size-h1: ${64 / 16}rem;
		--font-size-h2: ${32 / 16}rem;
	}

`;
export default GlobalStyle;
