import React from "react";
import styled from "styled-components";
import { Link, graphql, useStaticQuery } from "gatsby";
import scrollTo from "gatsby-plugin-smoothscroll";
import { MaxWidthWrapper } from "../styles/MaxWidthWrapper";
import gvdbLogo from "../assets/images/gvdb_logo_tr.png";
import { ThinkaholistFooter } from "./ThinkaholistFooter";

const FooterStyles = styled.footer`
  background-color: var(--color-base-dark);
  color: var(--color-white);
  padding: ${110 / 16}rem 0 2rem 0;
  margin-top: 1.5rem;
`;

const FooterContent = styled.div`
  display: flex;
  justify-content: space-between;
`;

const FooterList = styled.ul`
  /* display: flex;
  flex-direction: column;
  justify-content: space-between; */
  li {
    text-transform: uppercase;
    line-height: 2.8;
  }
  a {
    color: inherit;
    text-decoration: none;
    transition: box-shadow 0.2s ease;
    &:hover {
      box-shadow: 0px 2px 0px;
    }
  }
`;

const LogoContainer = styled.div`
  width: ${246 / 16}rem;
  height: ${246 / 16}rem;
  display: grid;
  place-items: center;
  /* A logo jobb széle kicsit kilóg(hat moblilon), 
  ezért kell ez ide */
  overflow: hidden;
  img {
    width: 100%;
    transform: scale(1.25);
  }
`;

export default function Footer() {
  const { links, site } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
          siteUrl
          description
        }
      }
      links: allContentfulFooterlink(sort: { fields: order, order: ASC }) {
        edges {
          node {
            id
            title
            reference
            anchor
            external
          }
        }
      }
    }
  `);

  const footerLinks = links.edges;
  return (
    <>
      <FooterStyles id='footer'>
        <MaxWidthWrapper>
          <FooterContent>
            <FooterList>
              {!footerLinks.length && <p>Cannot find footer links :-(</p>}
              {Array.isArray(footerLinks) &&
                footerLinks.map((link) => (
                  <li key={link.node.id}>
                    {link.node.external && (
                      <a
                        href={link.node.reference}
                        target='_blank'
                        rel='noreferrer noopener'
                      >
                        {link.node.title}
                      </a>
                    )}
                    {link.node.anchor && (
                      <a
                        href={`#0`}
                        onClick={() => scrollTo(`#${link.node.reference}`)}
                      >
                        {link.node.title}
                      </a>
                    )}
                    {!link.node.anchor && !link.node.external && (
                      <Link to={`/${link.node.reference}`}>
                        {link.node.title}
                      </Link>
                    )}
                  </li>
                ))}
            </FooterList>
            <LogoContainer>
              <img src={gvdbLogo} alt={`${site.siteMetadata.title} logo`} />
            </LogoContainer>
          </FooterContent>
        </MaxWidthWrapper>
      </FooterStyles>
      <ThinkaholistFooter />
    </>
  );
}
